import React, { FC } from 'react'
import clsx from 'clsx'
import Autosuggest from 'react-autosuggest'
import { ArrowDownIcon } from '../../icons/ArrowDown'
import { AutocompleteOption } from './parts'
import { AutocompleteProps } from './types'
import './Autocomplete.scss'
import { Typography } from '../../other/Typography'

const Autocomplete: FC<AutocompleteProps | any> = ({
  isRequired = false,
  adviceMsg,
  className,
  errorMsg,
  getSuggestionValue = (s): void => s.value,
  inputProps,
  icon = null,
  label,
  onSuggestionSelected,
  onSuggestionsClearRequested,
  onSuggestionsFetchRequested,
  renderSuggestion = AutocompleteOption,
  suggestions = [],
  autoFocus,
  fieldSize = 'L',
  ...rest
}) => {
  const messageByPriority = errorMsg || adviceMsg
  const withIcon = Boolean(icon)

  return (
    <div
      className={clsx(
        'Autocomplete',
        messageByPriority && 'Autocomplete_HasMsg',
        errorMsg && 'Autocomplete_Error',
        className,
        `Autocomplete_Size-${fieldSize}`,
      )}
    >
      <Autosuggest
        getSuggestionValue={getSuggestionValue}
        inputProps={inputProps}
        onSuggestionsClearRequested={onSuggestionsClearRequested}
        onSuggestionSelected={onSuggestionSelected}
        onSuggestionsFetchRequested={onSuggestionsFetchRequested}
        renderInputComponent={(props): any => (
          <>
            <input
              // eslint-disable-next-line jsx-a11y/no-autofocus
              autoFocus={autoFocus}
              {...props}
              className={clsx(props.className, {
                '--with-icon': withIcon,
              })}
            />
            {props.id && (
              <>
                <label
                  className={clsx('Autocomplete__Label', {
                    '--with-icon': withIcon,
                  })}
                  htmlFor={props.id}
                >
                  {label}
                  {isRequired && !props.value && (
                    <b className='FieldNext__Label_required'> *</b>
                  )}
                </label>
                {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
                {withIcon && (
                  <label className='Autocomplete__Icon' htmlFor={props.id}>
                    {icon}
                  </label>
                )}
                <label className='Autocomplete__ArrowIcon' htmlFor={props.id}>
                  <ArrowDownIcon />
                </label>
              </>
            )}
            {messageByPriority && (
              <Typography
                className='Autocomplete__Message'
                text={messageByPriority}
              />
            )}
          </>
        )}
        renderSuggestion={renderSuggestion}
        suggestions={suggestions}
        {...rest}
      />
    </div>
  )
}

export default Autocomplete
